body, h3, ul {
    margin: 0;
    padding: 0;
}
a {
    text-decoration: none;
}

/* sideBar */
.sideBar {
    background: #F5954B;
    position: fixed;
    top: 0;
    left: 0;
    min-width: 20%;
    height: 100%;
    padding: 50px 5px;
    z-index: 10;
    border: none;
}
.sideBar .sideBar_title {
    display: flex;
    justify-content: center;
    margin-bottom: 70px ;
}
.sideBar .sideBar_title .title_text {
    margin-left: 15px;
    font-size: 33px;
    font-weight: bold;
    color: white;
}

.sideBar .sideBar_title img {
    width: 40px;
    height: 40px;
}
/* subMenu */
.subMenu {
  width : 300px; 
  margin : 10px auto;
  font-weight: bold;
  
}

.subMenu span {
    margin-bottom: 5px;
    padding : 25px;
    background : white; 
    color : #2d2f31;
    font-size : 1.0em;
    -webkit-font-feature-settings : "smcp";
            font-feature-settings : "smcp";
    font-variant : small-caps;
    cursor : pointer;
    display: block;
    border-radius: 5px;
}

.subMenu span:hover {
    background: #2d2f31;
    transition-duration: 0.5s;
    color: white;
}

.subMenu span::after {
    float: right;
    right: 10%;
    content: "+";
}

.subMenu .slide {
    border-radius: 5px;
    clear:both;
    width:100%;
    height:0px;
    overflow: hidden;
    text-align: center;
    transition: height .4s ease;
}

.subMenu .slide button {
    padding: 20px;
    border: none;
    width: 100%;
    background: white;
    border-bottom: 1px solid #F5954B;
}
.subMenu .slide button:hover {
    background: #2d2f31;
    transition-duration: 0.5s;
    color: white;
}

#touch {
    position: absolute;
    opacity: 0;
    height: 0px;
}    

#touch:checked + .slide {
    height: 280px;
} 
/* subMenu end */

/* subInfoMenu */
.subInfoMenu {
    width : 300px; 
    margin : 10px auto;
    font-weight: bold;
    
  }
  
.subInfoMenu span {
    margin-bottom: 5px;
    padding : 25px;
    background : white; 
    color : #2d2f31;
    font-size : 1.0em;
    -webkit-font-feature-settings : "smcp";
            font-feature-settings : "smcp";
    font-variant : small-caps;
    cursor : pointer;
    display: block;
    border-radius: 5px;
}
  
.subInfoMenu span:hover {
      background: #2d2f31;
      transition-duration: 0.5s;
      color: white;
}
  
.subInfoMenu span::after {
      float: right;
      right: 10%;
      content: "+";
}
  
.subInfoMenu .slide {
    display: flex;
    position: fixed;
    bottom: 0px;
    left: 0px;
    min-width:100%;
    height:0px;
    overflow: hidden;
    text-align: center;
    transition: height .4s ease;
    background: white;
    justify-content: center;

}
.subInfoMenu .slide button {
    padding: 25px;
    border: none;
    width: 100%;
    border-radius: 5px;
}
.subInfoMenu .slide button:hover {
    background: #2d2f31;
    transition-duration: 0.5s;
    color: white;
}
.swiper-wrapper {
    display: flex;
}
#touchInfo {
    position: absolute;
    opacity: 0;
    height: 0px;
}    

#touchInfo:checked + .slide {
    height: 340px;
} 

.subInfoMenu .slide .subInfoMenu_item {
    width: 400px;
    height: 100%;
    margin: 10px 10px 0;
}
.subInfoMenu .slide .subInfoMenu_item .subInfoMenu_title {
    padding: 15px 0;
    background-color: #F5954B;
    color: white;
    font-size: 20px;
    font-weight: bold;
}
.subInfoMenu .slide .subInfoMenu_item .subInfoMenu_contents {
    display: flex;
    margin: 5px 0;
    text-align: left;
}
.subInfoMenu .slide .subInfoMenu_item .subInfoMenu_text {
    margin-left: 5px;
}
.subInfoMenu .slide .subInfoMenu_item .subInfoMenu_text .subInfoMenu_text_title {
    border-bottom: 2px solid #F5954B;
    font-weight: bold;
    font-size: 17px;
    padding-bottom: 5px;
    margin-bottom: 2px;
}
.subInfoMenu .slide .subInfoMenu_item .subInfoMenu_text .subInfoMenu_text_address {
    font-size: 13px;
    padding-bottom: 5px;
}
.subInfoMenu .slide .subInfoMenu_item .subInfoMenu_access_title {
    background-color: #F5954B;
    color: white;
    padding: 8px 0;
    font-size: 18px;
    font-weight: bold;
}
.subInfoMenu .slide .subInfoMenu_item .subInfoMenu_access_text {
    text-align: left;
    margin-top: 5px;
    font-size: 15px;
    line-height: 1.2;
}


  /* subInfoMenu end */


/*  sideBar end */
.search {
    /* transform: translateX(-50%); */
