    /* width: 100%; */
}

/* 検索半径 */
.container {
    display: none;
}
/* 検索半径 end */
/* locate */
.locate {
    position: absolute;
    background-color: white;
    box-shadow: 0 4px 5px 0 #00000026;
    border-radius: 5px;
    top: 20px;
    right: 10px;
    padding: 6px;
    border: none;
    z-index: 10;
    cursor: pointer;
}
.locate .locateIcon {
    width: 25px;
    height: 25px;
}
/* locate end */

.window_title {
    text-align: center;
    color: white;
    background-color: #F5954B;
    border-radius: 2px;
    margin-bottom: 10px;
    padding: 15px 0;
    font-size: 23px;
    font-weight: bold;
}

.window_middle {
    display: flex;
    margin-bottom: 10px;
}
.window_middle img {
    margin-right: 10px;
    width: 150px;
    /* height: 150px; */
}
.window_middle .middle_title {
    font-size: 19px;
    font-weight: bold;
    margin-bottom: 10px;
}
.window_footer {
    margin-bottom: 10px;
}
.window_footer .footer_subTitle {
    font-size: 15px;
    font-weight: bold;
}

.simplePage {
    display: none;
}


@media only screen and (max-width: 800px) {

    body, h3 {
        margin: 0;
        padding: 0;
    }
    a {
        text-decoration: none;
    }
    .sideBar {
        display: none;
    }

    .container {
        display: block;
        min-width: 100px;
        background-color: white;
        border-radius: 5px;
        box-shadow: 0 4px 5px 0 #00000026;
        position: absolute;
        z-index: 10;
        top: 20px;
        left: 15px;
    }
    #dropdown {
        left: 0;
        visibility: hidden;
        position: absolute;
    }
    .dropdownLabel {
        display: flex;
        justify-content: space-between;
        padding: 8px;
    }
    .dropdownLabel:hover {
        cursor: pointer;
    }
    .content {
        margin-top: 10px;
        display: none;
        position: absolute;
        width: 100%;
        left: 0;
        border-radius: 5px;
        background: white;
        box-shadow: 0 4px 5px 0 #00000026;
        text-align: center;
    }
    .content button {
        padding: 14px 0;
        border: none;
        width: 100%;
        background: #F5954B;
        font-weight: bold;
        font-size: 17px;
        color: white;
    }

    #dropdown:checked + label + div.content {
        display: block;
        border-top: 1px solid #00000026;
    }
    .cartIcon {
        margin-right: 10px;
        padding-top: 3px;
    }
    .caretIcon {
        transition: -webkit-transform 250ms ease-out;
        transition: transform 250ms ease-out;
        transition: transform 250ms ease-out, -webkit-transform 250ms ease-out;
        margin-left: 10px;
        padding-top: 4px;
    }
    #dropdown:checked + label > .caretIcon {
        -webkit-transform: rotate(-180deg);
                transform: rotate(-180deg);
    }

    .content ul {
        list-style-type: none;
        padding: 0 8px;
        margin: 0;
    }
    .content ul li {
        margin: 8px 0;
    }
    .locate {
        position: absolute;
        background-color: white;
        box-shadow: 0 4px 5px 0 #00000026;
        border-radius: 5px;
        top: 20px;
        right: 10px;
        padding: 6px;
        border: none;
        z-index: 10;
    }
    .locate .locateIcon {
        width: 25px;
        height: 25px;
    }

    .window_title {
        text-align: center;
        color: white;
        background-color: #F5954B;
        border-radius: 2px;
        margin-bottom: 10px;
        padding: 15px 0;
        font-size: 23px;
        font-weight: bold;
    }

    .window_middle {
        display: flex;
        margin-bottom: 10px;
    }
    .window_middle img {
        margin-right: 10px;
    }
    .window_middle b {
        font-size: 18px;
    }

    .simplePage {
        display: block;
        position: fixed;
        bottom: 0px;
        min-width: 100%;
        text-align: center;
        background-color: white;
        box-shadow: 10px 0 5px 0 #00000026;
    }

    .simple_content {
        overflow: hidden;
        display: flex;
        position: absolute;
        width: 100%;
        height: 400px;
        bottom: -400px;
        box-shadow: 10px 0 5px 0 #00000026;
        border-top-left-radius: 20px;
        border-top-right-radius: 20px;
        transition: -webkit-transform 250ms ease-out;
        transition: transform 250ms ease-out;
        transition: transform 250ms ease-out, -webkit-transform 250ms ease-out;
    }   
    .simple_content .item_wrap {
        padding: 20px 10px;
        width: 100%;
    }
    .simple_content .item_wrap .item_title {
        padding: 10px 0;
        background-color: #F5954B;
        color: white;
        font-size: 20px;
        font-weight: bold;
    }
    .simple_content .item_wrap .item_contents {
        display: flex;
        margin: 5px 0;
        text-align: left;
    }
    .simple_content .item_wrap .item_contents .item_text {
        margin-left: 5px;
    }
    .simple_content .item_wrap .item_contents .item_text .item_text_title {
        font-weight: bold;
        font-size: 16px;
        padding-bottom: 5px;
    }
    .simple_content .item_wrap .item_contents .item_text .item_text_address {
        font-size: 13px;
        padding-bottom: 5px;
    }
    .simple_content .item_wrap .item_access .item_access_title {
        background-color: #F5954B;
        color: white;
        padding: 10px 0;
        font-size: 20px;
        font-weight: bold;
    }
    .simple_content .item_wrap .item_access .item_access_text {
        text-align: left;
        margin-top: 5px;
    }

    #toggleBtn {
        position: fixed;
        left: -9999px;
    }
    label {
        cursor: pointer;
    }

    #toggleBtn:checked + label ~ .simple_content {
        background-color: white;
        bottom: 20px;
    }
    #toggleBtn:checked + label > .toggleBtn {
        -webkit-transform: rotate(-180deg);
                transform: rotate(-180deg);
    }
      
}
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
